body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: 'Sora', sans-serif;
}

h1{
  padding: 0;
  margin: 0;
}
.hero{
    background-color: "#DEE1E6FF"
}
.wittyLogo{
    width: 178px;
}

.logIn a{
    text-decoration: none !important;
}
.signUp a{
    text-decoration: none !important;
}

.topNav{
    border-bottom:solid 1px;
}
.searchBar{
    display: flex;
    justify-content: center;
}

.searchBarInputGroup{
    width: 544px;
}

.searchBarInput{
    border-radius: 30px 0 0 30px;
}

.searchBarIcon{
    border-radius: 0 30px 30px 0;
}
.imgDiv {
  padding-top: 6%;
}

.container-top {
  margin-top: "100px" !important;
}

.tagline-question {
  font-size: 24px;
  line-height: 40px;
  font-weight: 400;
  margin-bottom: 20px;
}

.tagline {
  margin-bottom: 25px;
}

.tagline h1 {
  font-size: 42px;
  line-height: 50px;
  color: #171a1fff;
  font-weight: 700;
}

@media (min-width: 320px) and (max-width: 480px) {
  .tagline h1 {
    font-size: 28px;
    line-height: 32px;
    color: #171a1fff;
    font-weight: 700;
  }
  .tagline-question {
    font-size: 18px;
    line-height: 40px;
    font-weight: 500;
    margin-bottom: 7px;
  }

  .body-top {
    padding-left: 12px;
  }
}

@media (min-width: 481px) and (max-width: 768px) {
  .tagline h1 {
    font-size: 31px;
    line-height: 34px;
    color: #171a1fff;
    font-weight: 700;
  }
  .tagline-question {
    font-size: 18px;
    line-height: 40px;
    font-weight: 500;
    margin-bottom: 7px;
  }

  .body-top {
    padding-left: 12px;
  }
}
@media (min-width: 769px) and (max-width: 1024px) {
    .tagline h1 {
        font-size: 31px;
        line-height: 34px;
        color: #171a1fff;
        font-weight: 700;
      }
      .tagline-question {
        font-size: 18px;
        line-height: 40px;
        font-weight: 500;
        margin-bottom: 7px;
      }
    
      .body-top {
        padding-left: 12px;
      }
}

